<template>
<div id="comNew">
  <div>
    <topbar :user="user"></topbar>
    <div class="appnav">
     <navigatior></navigatior>
    </div>
  </div>

      <div class="courseTab">

        <!--选项卡标签 -->
        <ul id="content-tab-header" class="nav nav-tabs" role="tablist">
          <li role="presentation" class="active">
            <router-link to="#" aria-controls="comnew" role="tab" data-toggle="tab">最新帖子</router-link>
          </li>
          <li role="presentation">
            <router-link to="/pages/comHot" role="tab">热门帖子</router-link>
          </li>
          <li role="presentation">
            <router-link to="/pages/comMy" role="tab">我的帖子</router-link>
          </li>

        </ul>

        <!--选项卡内容 -->
        <div id="content-tab-body" class="tab-content">

         <div role='tabpanel' class='tab-pane active content-tab-body-padding' id="subpanel">
          <div class="panel panel-default" v-for="(card, index) in cards">

            <div class="panel-heading" style="cursor:pointer;" @clcik="clickCard(card.cardId)">
              <span class="panel-title">
                 <span style="color:#F4A460">
                    {{card.comTitle}}
                 </span>
                 <span style="margin-left: 10%;font-size:12px;color:grey">
                    {{card.postUser}}
                 </span>
                 <span style="margin-left: 10%;font-size:12px;color:grey">
                    {{card.postTime}}
                 </span>
              </span>
            </div>

            <div class="panel-body" @clcik="clickCard(card.cardId)" style="cursor:pointer;">
              <p>{{card.comContent}}</p>

              <br />    
            </div>


            <div class='line'></div>
            <div class="clickTCC">
              <div @click="clickThumb(card.cardId, index)">
                 <img :src="thumbGreyIcon" v-show="isthumbGreyIcon[index]">
                 <img :src="thumbRedIcon" v-show="isthumbRedIcon[index]">
                 <span>{{card.thumbNum}}</span>
              </div>
              <div @clcik="clickCard(card.cardId)">
                 <img :src="commentIcon">
                 <span>{{card.commentNum}}</span>
              </div>
              <div @click="clickCollect(card.cardId, index)">
                 <img :src="collectGreyIcon" v-show="iscollectGreyIcon[index]">
                 <img :src="collectRedIcon" v-show="iscollectRedIcon[index]">
                 <span>{{card.collectNum}}</span>
              </div>
            </div>
           
          </div>

        </div>
      </div>
 </div>
</div>
</template>
<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import topbar from '@/components/topbar'
import navigatior from '@/components/navigatior'
import commentIcon from '@/assets/comment.png'
import thumbRedIcon from '@/assets/thumb-red.png'
import thumbGreyIcon from '@/assets/thumb-grey.png'
import collectRedIcon from '@/assets/collect-red.png'
import collectGreyIcon from '@/assets/collect-grey.png'
export default {
  data () {
    return {
      user: {},
      cards: [],
      commentIcon: commentIcon,
      thumbRedIcon: thumbRedIcon,
      thumbGreyIcon: thumbGreyIcon,
      collectRedIcon: collectRedIcon,
      collectGreyIcon: collectGreyIcon,
      isthumbGreyIcon: [],
      isthumbRedIcon: [],
      iscollectGreyIcon: [],
      iscollectRedIcon: [],
      // 弹出评论模态框
      iscommAlert: false,
      writeCommID: ''
    }
  },
  props: ['token'],
  components: {
    topbar, navigatior
  },
  methods: {
    dealIcon: function (id) {
      let self = this
      console.log('帖子详情获取--------->s2058')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/tiezi_detail',
        method: 'post',
        data: {
          type: 's2058',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('帖子详情接口获取结果----------->' + res.data.re + res.data.msg.post_msg)
        console.log(res.data.msg.post_critic)
        console.log(res.data.msg.post_likefavormsg)
        let temp = res.data.msg.post_likefavormsg
        let thumbgrey = false
        let thumbred = false
        let collectgrey = false
        let collectred = false
        if (temp[0] === 0) {
          thumbgrey = true
        } else {
          thumbred = true
        }
        if (temp[1] === 0) {
          collectgrey = true
        } else {
          collectred = true
        }
        self.isthumbGreyIcon.push(thumbgrey)
        self.isthumbRedIcon.push(thumbred)
        self.iscollectGreyIcon.push(collectgrey)
        self.iscollectRedIcon.push(collectred)
        console.log('dealIcon处理结果--------------->', thumbgrey, thumbred, collectgrey, collectred)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getCards: function () {
      let self = this
      console.log('获取帖子--------->s2049')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/show_tiezi',
        method: 'post',
        data: {
          type: 's2049',
          token: '3406ae3f44399522653232e2c24ac811',
          search: ''
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('获取到最新帖子数----------->' + res.data.re + res.data.msg.length)
        for (let i = 0; i < res.data.msg.length; i++) {
          self.dealIcon(res.data.msg[i].id)
          let temp = {
            cardId: res.data.msg[i].id,
            comTitle: res.data.msg[i].post_theme,
            postUser: res.data.msg[i].user_name,
            postTime: res.data.msg[i].post_time,
            comContent: res.data.msg[i].post_body,
            thumbNum: res.data.msg[i].like_amount,
            commentNum: res.data.msg[i].comment_amount,
            collectNum: res.data.msg[i].favor_amount
          }
          self.cards.push(temp)
        }
        console.log('cards.length---------->', self.cards.length)
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 点赞
    clickThumb: function (id, index) {
      let self = this
      console.log('点赞接口--------->s2051')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/likeordislike_tiezi',
        method: 'post',
        data: {
          type: 's2051',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('点赞结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '点赞成功') {
          self.isthumbGreyIcon[index] = false
          self.isthumbRedIcon[index] = true
          self.cards[index].thumbNum ++
        } else {
          self.isthumbGreyIcon[index] = true
          self.isthumbRedIcon[index] = false
          self.cards[index].thumbNum --
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 点击收藏
    clickCollect: function (id, index) {
      let self = this
      console.log('收藏接口--------->s2052')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/favor_tiezi',
        method: 'post',
        data: {
          type: 's2052',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('收藏结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '收藏成功') {
          self.iscollectGreyIcon[index] = false
          self.iscollectRedIcon[index] = true
          self.cards[index].collectNum ++
        } else {
          self.iscollectGreyIcon[index] = true
          self.iscollectRedIcon[index] = false
          self.cards[index].collectNum --
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 点击进入详情页或点击评论进入详情页进行评论
    clickCard: function (id) {
      console.log('--------->进入帖子详情页')
      this.$router.push({path: '/pages/discuss/comment/cardDetail/' + id})
    }
  },
  created () {
    this.user = store.fetch('user')
    this.getCards()
  }
}
</script>
<style type="text/css" scoped>
.appnav {
  width:15%;
}
.courseTab {
  width: 80%;
  margin-right: 2.5%;
  margin-top: 20px;
  float: right;
}
/*小面板*/
#subpanel {
  width:95%;
  margin-left:2.5%;
  margin-top: 10px;
}
img {
  width:25px;
  height:25px;
  padding: 0px;
}
.line{
    height: 2px;
    width: 95%;
    background-image: linear-gradient(to right,rgba(221, 221, 221, 0.3), rgba(221, 221, 221, 1),rgba(221, 221, 221, 0.3));
    margin-left: 2.5%;
  }
.clickTCC {
  width:100%;
  margin: 5px auto;
  text-align: right;
}
.clickTCC div{
  display:inline;
  margin-right:12px;
}
.clickTCC img{
  width:20px;
  height:20px;
  padding: 0px;
}

</style>


