<template>
<div id="cardDetail">

<!-- header -->
<div>
    <topbar :user="user"></topbar>
</div>


<!-- 标签和返回按钮 -->
 <!-- 标签链接 -->
  <div style="width:90% text-align: center;margin-top:20px;margin-left:5%;">
    <h5 class="text-primary">
      <router-link to="/pages/discuss/comment/comNew">
         <span>>交流社区</span>
      </router-link>
      <span>>帖子详情</span>
    </h5>
    </div>
<router-link to="/pages/discuss/comment/comNew" class="link-back"><img :src="iconreback"></router-link>






<!-- 帖子详情内容 -->
<div class="panel panel-info" id="cardContent">
  <div class="panel-body">
    <div class="card-title">
       <div class="text-center" style="width:100%;font-size:20px;color:#ffa955;">{{detailCon.post_theme}}</div>
       <h6 class="text-center" style="color:grey;">
         <span>
           {{detailCon.user_name}}
         </span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
         <span>
            {{detailCon.post_time}}
         </span>
       </h6>
       <p>
       {{detailCon.post_body}}
       </p>  
    </div> 
  </div>



   <div class='line'></div>
   <!-- 点赞收藏 -->
       <div class="clickTCC">
          <div @click="clickThumb(detailCon.id)">
            <img :src="thumbGreyIcon" v-show="isthumbGreyIcon">
            <img :src="thumbRedIcon" v-show="isthumbRedIcon">
            <span>{{detailCon.like_amount}}</span>
          </div>
          <div @clcik="clickComment('#commArea')">
            <img :src="commentIcon">
            <span>{{detailCon.comment_amount}}</span>
          </div>
          <div @click="clickCollect(detailCon.id)">
            <img :src="collectGreyIcon" v-show="iscollectGreyIcon">
            <img :src="collectRedIcon" v-show="iscollectRedIcon">
            <span>{{detailCon.favor_amount}}</span>
          </div>
      </div>
       
</div>


<!-- 评论区 -->
<div class="panel panel-info" id="commentArea">
  <div class="panel-heading">
    <h3 class="panel-title">评论</h3>
  </div>
  <div class="panel-body">
    <div v-for="dcom in detailComm">
       <div style="margin-bottom:5px;"><strong>{{dcom.critic_name}}</strong>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<small style="color:grey;">{{dcom.critic_date}}</small></div>
       <div class="panel panel-default panel-body" style="width:95%;margin-left:2.5%;">
         {{dcom.critic_comment}}
       </div>
    </div>
  </div>
</div>

<!-- 写评论 -->
<div id="writeComm">
<div class="panel panel-warning">
  <div class="panel-heading">
    <h3 class="panel-title">我来评论一下</h3>
  </div>
  <div class="panel-body">
    <textarea class="form-control" rows="5" placeholder="输入您的评论内容" v-model="myComm"></textarea>
    <div style="width:100%;text-align:center;margin-top:10px;">
         <button class="btn btn-warning" style="width:100px;" @click="submitComm()">发表评论</button>
    </div>
  </div>
</div>
</div>


</div>

</template>

<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import topbar from '@/components/topbar'
import navigatior from '@/components/navigatior'
import commentIcon from '@/assets/comment.png'
import thumbRedIcon from '@/assets/thumb-red.png'
import thumbGreyIcon from '@/assets/thumb-grey.png'
import collectRedIcon from '@/assets/collect-red.png'
import collectGreyIcon from '@/assets/collect-grey.png'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      user: {},
      cardId: this.$route.params.id,
      detailCon: {}, // 帖子内容
      detailComm: [], // 帖子评论
      commentIcon: commentIcon,
      thumbRedIcon: thumbRedIcon,
      isthumbRedIcon: false,
      thumbGreyIcon: thumbGreyIcon,
      isthumbGreyIcon: true,
      collectRedIcon: collectRedIcon,
      iscollectRedIcon: false,
      collectGreyIcon: collectGreyIcon,
      iscollectGreyIcon: true,
      myComm: '',
      iconreback: iconreback
    }
  },
  props: ['token'],
  components: {
    topbar, navigatior
  },
  methods: {
    getCardDetail: function () {
      let self = this
      console.log('帖子详情获取--------->s2058')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/tiezi_detail',
        method: 'post',
        data: {
          type: 's2058',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: self.cardId
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('帖子详情接口获取结果----------->', res.data.re, res.data.msg.post_msg[0])
        console.log('帖子点评信息-------------->', res.data.msg.post_critic)
        console.log('帖子我的点评情况-------------->', res.data.msg.post_likefavormsg)
        self.detailCon = res.data.msg.post_msg[0]
        self.detailComm = res.data.msg.post_critic
        if (res.data.msg.post_likefavormsg[0] === 0) {
          self.isthumbGreyIcon = true
          self.isthumbRedIcon = false
        } else {
          self.isthumbGreyIcon = false
          self.isthumbRedIcon = true
        }
        if (res.data.msg.post_likefavormsg[1] === 0) {
          self.iscollectGreyIcon = true
          self.iscollectRedIcon = false
        } else {
          self.iscollectGreyIcon = false
          self.iscollectRedIcon = true
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 点赞
    clickThumb: function (id) {
      let self = this
      console.log('点赞接口--------->s2051')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/likeordislike_tiezi',
        method: 'post',
        data: {
          type: 's2051',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('点赞结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '点赞成功') {
          self.isthumbRedIcon = true
          self.isthumbGreyIcon = false
          self.detailCon.like_amount ++
        } else {
          self.isthumbGreyIcon = true
          self.isthumbRedIcon = false
          self.detailCon.like_amount --
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 点击收藏
    clickCollect: function (id) {
      let self = this
      console.log('收藏接口--------->s2052')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/favor_tiezi',
        method: 'post',
        data: {
          type: 's2052',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('收藏结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '收藏成功') {
          self.iscollectRedIcon = true
          self.iscollectGreyIcon = false
          self.detailCon.favor_amount ++
        } else {
          self.iscollectGreyIcon = true
          self.iscollectRedIcon = false
          self.detailCon.favor_amount --
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 点击评论icon
    clickComment: function () {
      var anchor = this.$el.querySelector('#writeComm')
      document.body.scrollTop = anchor.offsetTop
    },
    // 写完评论后点击发表
    submitComm: function () {
      let self = this
      console.log('发表评论的接口--------->s2053')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/post_comment',
        method: 'post',
        data: {
          type: 's2053',
          token: '3406ae3f44399522653232e2c24ac811',
          critic_comment: self.myComm,
          tiezi_id: self.cardId
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('发表评论结果----------->', res.data.re, res.data.msg)
        if (res.data.msg === '评论成功') {
          alert('评论成功')
        } else {
          alert('评论失败')
        }
      }).catch(function (err) {
        console.log(err)
      })
      // 刷新一下页面
      window.location.reload()
    }
  },
  created () {
    this.user = store.fetch('user')
    this.getCardDetail()
  }
}
</script>

<style type="text/css" scoped>
#cardContent {
  margin-top:20px;
}
#cardContent,#commentArea,#writeComm{
  width:90%;
  margin-left:5%;
}
.link-back{
  float:right;
  width:150px;
  margin-top: -35px;
  margin-right: 2.5%;
}
.link-back img{
  float: right;
  width: 30px;
  height: 30px;
}
.line{
    height: 2px;
    width: 95%;
    background-image: linear-gradient(to right,rgba(221, 221, 221, 0.3), rgba(221, 221, 221, 1),rgba(221, 221, 221, 0.3));
    margin-left: 2.5%;
  }
.clickTCC {
  width:100%;
  margin: 5px auto;
  text-align: right;
}
.clickTCC div{
  display:inline;
  margin-right:12px;
}
.clickTCC img{
  width:20px;
  height:20px;
  padding: 0px;
}
a{
  text-decoration: none;
}

</style>


