<template>
	<div>
		<div>
    <topbar :user="user"></topbar>
    <div class="appnav">
      
      <navigatior></navigatior>
      </div>
      </div>

      <div style="margin-top: 5px; margin-left: 210px;">
      <div data-spy="scroll" data-target="#navbar-example" data-offset="0" 
   style="height:50px;overflow-x:hidden; position: relative;">
        <ul class="nav nav-pills">
      <li class="active">
      <a href="" data-toggle="tab" @click="changeTab(1)">
       最新</a>
  </li>
      <li>
      <a href="" data-toggle="tab" @click="changeTab(2)">
       最热门</a>
  </li>
  <li>
      <a href="" data-toggle="tab" @click="changeTab(3)">
       我的帖子</a>
  </li>
</ul>
</div>
      </div>
      <div>
      	<p>{{new_post}}</p>
      </div>
	</div>
</template>

<script type="text/javascript">
import topbar from '@/components/topbar'
import navigatior from '@/components/navigatior'
import store from '@/store.js'
import axios from 'axios'
export default {
  props: ['token'],
  data () {
    return {
      user: {},
      status: '',
      new_post: [],
      hot_post: [],
      my_publish: [],
      my_collect: []
    }
  },
  methods: {
    changeTab: function (type) {
      this.status = type
    },
    showPost: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/show_tiezi',
        method: 'post',
        data: {
          type: 's2049',
          token: self.token,
          search: ''
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.new_post = res.data.msg
        console.log('new_post---------------------->', self.new_post)
      }).catch(function (err) {
        console.log(err)
      })
    },
    showHotPost: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/teacher/show_hotpost',
        method: 'post',
        data: {
          type: 'T5004',
          token: self.token
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.hot_post = res.data.msg
        console.log('hot_post---------------------->', self.hot_post)
      }).catch(function (err) {
        console.log(err)
      })
    },
    showMyPost: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/teacher/show_mypost',
        method: 'post',
        data: {
          type: 'T5005',
          token: self.token
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.my_publish = res.data.msg.我發表的帖子
        self.my_collect = res.data.msg.我收藏的帖子
        console.log('my_publish---------------------->', self.my_publish)
        console.log('my_collect---------------------->', self.my_collect)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  watch: {
    status: function (val, oldval) {
      console.log('new: %s, old: %s', val, oldval)
      if (val === 1) {
        this.showPost()
      } else if (val === 2) {
        this.showHotPost()
      } else {
        this.showMyPost()
      }
    }
  },
  components: {
    topbar, navigatior
  },
  created () {
    this.user = store.fetch('user')
    this.showPost()
  }
}
</script>

<style type="text/css">
	
</style>


