<template>
<div id="comMy">
   <div>
    <topbar :user="user"></topbar>
    <div class="appnav">
     <navigatior></navigatior>
    </div>
   </div>

      <div class="courseTab">

        <!--选项卡标签 -->
        <ul id="content-tab-header" class="nav nav-tabs" role="tablist">
          <li role="presentation">
            <router-link to="/pages/comNew" role="tab" data-toggle="tab">最新帖子</router-link>
          </li>
          <li role="presentation">
            <router-link to="/pages/comHot" role="tab" data-toggle="tab">热门帖子</router-link>
          </li>
          
          <li role="presentation" class="active">
            <router-link to="#" aria-controls="commy" role="tab" data-toggle="tab">我的帖子</router-link>
          </li>

        </ul>

        <!-- 发帖子 -->
<div id="commArea">
<div class="panel panel-warning">
  <div class="panel-heading">
    <h3 class="panel-title">我来发个帖子</h3>
  </div>
  <div class="panel-body">
    标题：<input class="form-control" v-model="commyTitle" style="margin-bottom:5px;">
    内容：
    <textarea class="form-control" rows="5" style="margin-bottom:10px;" placeholder="输入您的帖子内容" v-model="myComm"></textarea>
    <center style="padding:0px;"><button style="width:150px;font-size:15px;" type="button" class="btn btn-warning" @click="submitCard()">发&nbsp;&nbsp;表</button></center>
  </div>
</div>
</div>


        <!--选项卡内容 -->
        <div id="content-tab-body" class="tab-content">

         <div role='tabpanel' class='tab-pane active content-tab-body-padding' id="subpanel">
          <div class="panel panel-default" v-for="card in cards">

            <div class="panel-heading">
              <span class="panel-title" >
                 <span @clcik="clickCard(card.cardId)">
                    {{card.comTitle}}
                 </span>
                 <span style="margin-left: 10%;font-size:12px;color:grey">
                    {{card.postUser}}
                 </span>
                 <span style="margin-left: 10%;font-size:12px;color:grey">
                    {{card.postTime}}
                 </span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                 <span @click="deleteCard(card.cardId)" style="float:right;font-size:15px;color:red;cursor:pointer;">
                    删除
                 </span>
              </span>
            </div>

            <div class="panel-body" @clcik="clickCard(card.cardId)">
              <p>{{card.comContent}}</p>
              <br />
            </div>

            <div class='line'></div>
            <div class="clickTCC">
              <div @click="clickThumb(card.cardId)">
                 <img :src="thumbGreyICon" v-show="isthumbGreyICon">
                 <img :src="thumbRedICon" v-show="isthumbRedICon">
                 <span>{{card.thumbNum}}</span>
              </div>
              <div @clcik="clickCard(card.cardId)">
                 <img :src="commentIcon">
                 <span>{{card.commentNum}}</span>
              </div>
              <div @click="clickCollect(card.cardId)">
                 <img :src="collectGreyIcon" v-show="iscollectGreyIcon">
                 <img :src="collectRedIcon" v-show="iscollectRedIcon">
                 <span>{{card.collectNum}}</span>
              </div>
            </div>

          </div>

        </div>
      </div>
 </div>




</div>

</template>
<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import topbar from '@/components/topbar'
import navigatior from '@/components/navigatior'
import commentIcon from '@/assets/comment.png'
import thumbRedICon from '@/assets/thumb-red.png'
import thumbGreyICon from '@/assets/thumb-grey.png'
import collectRedIcon from '@/assets/collect-red.png'
import collectGreyIcon from '@/assets/collect-grey.png'
export default {
  data () {
    return {
      user: {},
      cards: [],
      commentIcon: commentIcon,
      thumbRedICon: thumbRedICon,
      isthumbRedICon: false,
      thumbGreyICon: thumbGreyICon,
      isthumbGreyICon: true,
      collectRedIcon: collectRedIcon,
      iscollectRedIcon: false,
      collectGreyIcon: collectGreyIcon,
      iscollectGreyIcon: true,
      // 弹出评论模态框
      iscommAlert: false,
      writeCommID: '',
      commyTitle: '',
      myComm: ''
    }
  },
  props: ['token'],
  components: {
    topbar, navigatior
  },
  methods: {
    getCards: function () {
      let self = this
      console.log('获取我的帖子--------->s2049')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/show_tiezi',
        method: 'post',
        data: {
          type: 's2049',
          token: '3406ae3f44399522653232e2c24ac811',
          search: ''
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('获取到我的帖子数----------->' + res.data.re + res.data.msg.length)
        for (let i = 0; i < res.data.msg.length; i++) {
          let temp = {
            cardId: res.data.msg[i].id,
            comTitle: res.data.msg[i].post_theme,
            postUser: res.data.msg[i].user_name,
            postTime: res.data.msg[i].post_time,
            comContent: res.data.msg[i].post_body,
            thumbNum: res.data.msg[i].like_amount,
            commentNum: res.data.msg[i].comment_amount,
            collectNum: res.data.msg[i].favor_amount
          }
          self.cards.push(temp)
        }
        console.log('cards.length---------->', self.cards.length)
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 点赞
    clickThumb: function (id) {
      let self = this
      console.log('点赞接口--------->s2051')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/likeordislike_tiezi',
        method: 'post',
        data: {
          type: 's2051',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('点赞结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '点赞成功') {
          self.isthumbRedICon = true
          self.isthumbGreyICon = false
        } else {
          self.isthumbGreyICon = true
          self.isthumbRedICon = false
        }
      }).catch(function (err) {
        console.log(err)
      })
       // 刷新一下页面
      window.location.reload()
    },
    // 点击收藏
    clickCollect: function (id) {
      let self = this
      console.log('收藏接口--------->s2052')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/favor_tiezi',
        method: 'post',
        data: {
          type: 's2052',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('收藏结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '收藏成功') {
          self.iscollectRedIcon = true
          self.iscollectGreyIcon = false
        } else {
          self.iscollectGreyIcon = true
          self.iscollectRedIcon = false
        }
      }).catch(function (err) {
        console.log(err)
      })
       // 刷新一下页面
      window.location.reload()
    },
    // 点击进入详情页或点击评论进入详情页进行评论
    clickCard: function (id) {
      this.$router.push({path: '/pages/discuss/comment/cardDetail/' + id})
    },
    // 点击发表我的帖子
    submitCard: function () {
      // 连接口
      let self = this
      console.log('上传我的帖子接口--------->s2050')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/post_tiezi',
        method: 'post',
        data: {
          type: 's2050',
          token: '3406ae3f44399522653232e2c24ac811',
          post_theme: self.commyTitle,
          post_body: self.myComm
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('帖子上传结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '发表成功') {
          alert('发表成功')
        } else {
          alert('发表失败')
        }
      }).catch(function (err) {
        console.log(err)
      })
      // 刷新一下页面
      window.location.reload()
    },
    // 点击删除自己的帖子
    deleteCard: function (id) {
      console.log('删除我的帖子接口--------->s2057')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/remove_tiezi',
        method: 'post',
        data: {
          type: 's2057',
          token: '3406ae3f44399522653232e2c24ac811',
          tiezi_id: id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('我的帖子删除结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '這不是您的帖子喲') {
          alert('這不是您的帖子喲')
        } else {
          alert('删除成功')
        }
      }).catch(function (err) {
        console.log(err)
      })
       // 刷新一下页面
      window.location.reload()
    },
    // 删除评论
    deleteComm: function (cardid) {
      console.log('删除我的评论接口--------->s2054')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/remove_comment',
        method: 'post',
        data: {
          type: 's2054',
          token: '3406ae3f44399522653232e2c24ac811',
          comment_id: '1',
          tiezi_id: cardid
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('我的评论删除结果----------->' + res.data.re + res.data.msg)
        if (res.data.msg === '刪除成功') {
          alert('刪除成功')
        } else {
          alert('删除失败')
        }
      }).catch(function (err) {
        console.log(err)
      })
       // 刷新一下页面
      window.location.reload()
    }
  },
  created () {
    this.user = store.fetch('user')
    this.getCards()
  }
}
</script>
<style type="text/css" scoped>
.appnav {
  width:15%;
}
.courseTab {
  width: 80%;
  margin-right: 2.5%;
  margin-top: 20px;
  float: right;
}
#subpanel,#commArea {
  width:95%;
  margin-left:2.5%;
  margin-top: 10px;
}
.line{
    height: 2px;
    width: 95%;
    background-image: linear-gradient(to right,rgba(221, 221, 221, 0.3), rgba(221, 221, 221, 1),rgba(221, 221, 221, 0.3));
    margin-left: 2.5%;
  }
.clickTCC {
  width:100%;
  margin: 5px auto;
  text-align: right;
}
.clickTCC div{
  display:inline;
  margin-right:12px;
}
.clickTCC img{
  width:20px;
  height:20px;
  padding: 0px;
}

</style>


